<template>
  <v-card-text>
    <v-container>
      <v-row>
        <v-col>
          <v-text-field
            v-model="role.title"
            :label="$t('forms.labels.title')"
            outlined
            :disabled="!notAllowedToChange"
            :rules="[rules.required]"
          ></v-text-field>
          <div v-if="notAdmin">
            <h2 class="mb-5 mt-2">Page Permissions</h2>
            <div v-for="(route, index) in routes" :key="index" class="mt-4">
              <div
                v-if="checkPossibility(route.name ? route.name : route.path)"
              >
                <v-switch
                  v-model="role.pagePermissions"
                  :value="getName(route.name ? route.name : route.path)"
                  :label="getName(route.name ? route.name : route.path)"
                ></v-switch>
                <div v-for="(rout, inde) in route.children" :key="inde">
                  <div
                    v-if="
                      getName(rout.name ? rout.name : rout.path) !==
                        getName(route.name ? route.name : route.path)
                    "
                  >
                    <v-switch
                      v-model="role.pagePermissions"
                      :value="rout.name ? rout.name : rout.path"
                      :label="rout.name ? rout.name : rout.path"
                    ></v-switch>
                  </div>
                  <div v-for="(rou, ind) in rout.children" :key="ind">
                    <div
                      v-if="
                        getName(rout.name ? rout.name : rout.path) !==
                          getName(rou.name ? rou.name : rou.path)
                      "
                    >
                      <v-switch
                        v-model="role.pagePermissions"
                        :value="rou.name ? rou.name : rou.path"
                        :label="rou.name ? rou.name : rou.path"
                      ></v-switch>
                    </div>
                    <div v-for="(ro, i) in rou.children" :key="i">
                      <v-switch
                        v-model="role.pagePermissions"
                        :value="ro.name ? ro.name : ro.path"
                        :label="ro.name ? ro.name : ro.path"
                      ></v-switch>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </v-col>
      </v-row>
    </v-container>
  </v-card-text>
</template>

<script>
import validations from '@/mixins/validations'
export default {
  mixins: [validations],
  props: {
    role: {
      type: Object,
      required: true
    },
    edit: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      routes: this.$router.options.routes
    }
  },
  computed: {
    notAllowedToChange() {
      if (
        this.role &&
        this.role.title &&
        (this.role.title === 'admin' || this.role.title === 'user')
      ) {
        return false
      }
      return true
    },
    notAdmin() {
      if (this.role && this.role.title && this.role.title === 'admin') {
        return false
      }
      return true
    }
  },
  methods: {
    getName(name) {
      let n

      if (name.startsWith('/')) {
        n = name.replace('/', '')
      } else {
        n = name
      }
      return n
    },
    checkPossibility(route) {
      if (route.startsWith('/dashboard')) {
        return true
      }
      return false
    }
  }
}
</script>

<style></style>
